                "verse": 21,
                "text": "Y los días de los hijos de los hombres fueron prolongados, según la voluntad de Dios, para que se arrepintiesen mientras se hallaran en la carne; por lo tanto, su estado llegó a ser un estado de probación, y su tiempo fue prolongado, conforme a los mandamientos que el Señor Dios dio a los hijos de los hombres. Porque él dio el mandamiento de que todos los hombres se arrepintieran; pues mostró a todos los hombres que estaban perdidos a causa de la transgresión de sus padres.",
                "reference": "Nefi 2 2:21"
