                "text": "Y el Mesías vendrá en la plenitud de los tiempos, a fin de redimir a los hijos de los hombres de la caída. Y porque son redimidos de la caída, han llegado a quedar libres para siempre, discerniendo el bien del mal, para actuar por sí mismos, y no para que se actúe sobre ellos, a menos que sea por el castigo de la ley en el grande y último día, según los mandamientos que Dios ha dado.",
