                "verse": 25,
                "text": "Y mi cuerpo ha sido conducido en las alas de su Espíritu hasta montañas muy altas; y mis ojos han visto grandes cosas, sí, demasiado grandes para el hombre; por lo tanto, se me mandó que no las escribiera.",
