                "text": "Yo, Zeniff, habiendo sido instruido en todo el idioma de los nefitas y habiendo tenido conocimiento de la tierra de Nefi, o sea, la tierra de la primera herencia de nuestros padres, y habiendo sido enviado como espía entre los lamanitas para que observase sus fuerzas —a fin de que nuestro ejército cayera sobre ellos y los destruyera— mas cuando vi lo bueno que había entre ellos, no quise que fuesen destruidos.",
