                "verse": 16,
                "text": "Porque son muchas las promesas que se extienden a los lamanitas; pues es por causa de las tradiciones de sus padres que han permanecido en su estado de ignorancia; por tanto, el Señor les será misericordioso y prolongará su existencia en la tierra.",
