                "verse": 17,
                "text": "Y un día se les persuadirá a creer en su palabra, y a saber de la incorrección de las tradiciones de sus padres; y muchos de ellos se salvarán, porque el Señor será misericordioso con todos los que invocaren su nombre.",
                "reference": "Alma 9:17"
