                "text": "sí, y después que Dios los rescató de la tierra de Jerusalén por la mano del Señor; después de haber sido librados del hambre y de la enfermedad, y de todo género de dolencias de toda clase; después de haber sido fortalecidos en la guerra para que no fuesen destruidos; después de haber sido librados del cautiverio una vez tras otra, y guardados y preservados hasta hoy; y han sido prosperados hasta ser ricos en todas las cosas;",
                "reference": "Alma 9:22"
