                "text": "Y aconteció que en el año cuarenta y dos del gobierno de los jueces, después que Moroníah hubo restablecido la paz entre los nefitas y los lamanitas, he aquí que no había quien ocupase el asiento judicial; por tanto, empezó a haber de nuevo una contención entre el pueblo concerniente a quién debía ocupar el puesto.",
