                "text": "No obstante, Helamán ocupó el asiento judicial con justicia y equidad; sí, se esforzó por observar los estatutos, y los juicios, y los mandamientos de Dios; e hizo lo que era recto a la vista de Dios continuamente; y anduvo en las vías de su padre, de tal modo que prosperó en la tierra.",
