                "verse": 23,
                "text": "Y sucedió que en el año cuarenta y nueve del gobierno de los jueces se estableció una paz continua en la tierra, todo menos las combinaciones secretas que Gadiantón, el ladrón, había establecido en las partes más pobladas de la tierra, combinaciones que en aquel tiempo no eran del conocimiento de aquellos que estaban a la cabeza del gobierno; por tanto, no fueron destruidas.",
