                "verse": 50,
                "text": "Y ocurrió que fueron, y ejercieron su ministerio entre el pueblo, declarando en todas las regiones inmediatas las cosas que habían oído y visto, de tal manera que se convencieron de ellas la mayor parte de los lamanitas, a causa de la grandeza de las evidencias que habían recibido.",
                "reference": "Helamán 5:50"
