                "text": "No obstante, los miembros de la iglesia se alegraron muchísimo por la conversión de los lamanitas, sí, por la iglesia de Dios que se había establecido entre ellos. Y unos y otros se hermanaron, y se regocijaron unos con otros, y sintieron gran gozo.",
