                "verse": 21,
                "text": "Mas he aquí, Satanás incitó el corazón de la mayoría de los nefitas, a tal grado que se unieron a esas bandas de ladrones, y participaron en sus pactos y sus juramentos de que se protegerían y se preservarían unos a otros en cualesquiera circunstancias difíciles en que se encontrasen, a fin de que no fuesen castigados por sus asesinatos, y sus robos, y sus hurtos.",
