                "text": "Y el Señor mandó al hermano de Jared que descendiera del monte, de la presencia del Señor, y escribiera las cosas que había visto; y fue prohibido que se dieran a los hijos de los hombres, sino hasta después que él fuese levantado sobre la cruz; y por esta causa las guardó el rey Mosíah, para que no llegasen al mundo sino hasta después que Cristo se manifestara a su pueblo.",
