                "text": "He aquí, cuando rasguéis ese velo de incredulidad que os hace permanecer en vuestro espantoso estado de iniquidad, y dureza de corazón, y ceguedad de mente, entonces las cosas grandes y maravillosas que han estado ocultas de vosotros desde el principio del mundo, sí, cuando invoquéis al Padre en mi nombre, con un corazón quebrantado y un espíritu contrito, entonces sabréis que el Padre se ha acordado del convenio que hizo con vuestros padres, oh casa de Israel.",
                "reference": "Éter 4:15"
