                "text": "Arrepentíos, pues, todos vosotros los extremos de la tierra, y venid a mí, y creed en mi evangelio y sed bautizados en mi nombre; porque el que crea y sea bautizado, será salvo; mas el que no crea, será condenado; y las señales seguirán a los que crean en mi nombre.",
