                "verse": 4,
                "text": "Y cuando Corihor tenía treinta y dos años de edad, se rebeló contra su padre, y fue y habitó en la tierra de Nehor; y engendró hijos e hijas, los cuales fueron muy bellos; por tanto, Corihor atrajo a muchos en pos de él.",
