                "text": "Y sucedió que Kib vivió en el cautiverio, así como su pueblo, bajo su hijo Corihor, hasta llegar a una edad muy avanzada; no obstante, Kib engendró a Shule en su vejez, mientras todavía se hallaba cautivo.",
