                "verse": 19,
                "text": "Y hubo muchos cuya fe era tan sumamente fuerte, aun antes de la venida de Cristo, que no se les pudo impedir penetrar el velo, sino que realmente vieron con sus propios ojos las cosas que habían visto con el ojo de la fe; y se regocijaron.",
                "reference": "Éter 12:19"
