                "verse": 20,
                "text": "Y he aquí, hemos visto en estos anales que uno de estos fue el hermano de Jared; porque tan grande era su fe en Dios, que cuando Dios extendió su dedo, no lo pudo ocultar de la vista del hermano de Jared, por motivo de la palabra que le había hablado, palabra que había logrado por medio de la fe.",
