                "verse": 23,
                "text": "Y le dije: Señor, los gentiles se burlarán de estas cosas, debido a nuestra debilidad en escribir; porque tú, Señor, nos has hecho fuertes en palabras por la fe, pero no nos has hecho fuertes para escribir; porque concediste que todos los de este pueblo declarasen mucho, por motivo del Espíritu Santo que tú les has dado;",
                "reference": "Éter 12:23"
